    </div>

    <div class="diff-controls">
      <el-radio-group v-model="diffMode" @change="performDiff">
        <el-radio-button label="line">é€è¡Œå¯¹æ¯”</el-radio-button>
        <el-radio-button label="word">å•è¯å¯¹æ¯”</el-radio-button>
        <el-radio-button label="char">å­—ç¬¦å¯¹æ¯”</el-radio-button>
      </el-radio-group>
      
      <el-checkbox v-model="ignoreWhitespace" @change="performDiff">å¿½ç•¥ç©ºç™½å­—ç¬¦</el-checkbox>
      <el-checkbox v-model="ignoreCase" @change="performDiff">å¿½ç•¥å¤§å°å†™</el-checkbox>
      
      <el-button @click="swapTexts">
