      </el-button>
    </div>

    <el-row :gutter="20">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="text-card">
          <div slot="header">
            <span>åŸå§‹æ–‡æœ¬</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="loadSampleText('original')">
              åŠ è½½ç¤ºä¾‹
            </el-button>
          </div>
          
          <el-input
            type="textarea"
            v-model="originalText"
            placeholder="è¯·è¾“å…¥åŸå§‹æ–‡æœ¬"
            :rows="15"
            @input="performDiff"
          ></el-input>
          
          <div class="text-stats">
            <span>è¡Œæ•°: {{ getLineCount(originalText) }}</span>
            <span>å­—ç¬¦æ•°: {{ originalText.length }}</span>
            <span>å•è¯æ•°: {{ getWordCount(originalText) }}</span>
          </div>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="text-card">
          <div slot="header">
            <span>ä¿®æ”¹æ–‡æœ¬</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="loadSampleText('modified')">
              åŠ è½½ç¤ºä¾‹
            </el-button>
          </div>
          
          <el-input
            type="textarea"
            v-model="modifiedText"
            placeholder="è¯·è¾“å…¥ä¿®æ”¹åçš„æ–‡æœ¬"
            :rows="15"
            @input="performDiff"
          ></el-input>
          
          <div class="text-stats">
            <span>è¡Œæ•°: {{ getLineCount(modifiedText) }}</span>
            <span>å­—ç¬¦æ•°: {{ modifiedText.length }}</span>
