          </div>
        </div>
      </div>
      
      <div v-else class="no-diff">
        <i class="el-icon-success"></i>
        <p>ä¸¤æ®µæ–‡æœ¬å®Œå…¨ç›¸åŒ</p>
      </div>
    </el-card>

    <el-card class="summary-card" style="margin-top: 20px;">
      <div slot="header">
