<script>
export default {
  name: 'TextDiffView',
  metaInfo: {
    title: 'æ–‡æœ¬å·®å¼‚å¯¹æ¯”å·¥å…· - åœ¨çº¿æ–‡æœ¬å¯¹æ¯”',
    meta: [
      { name: 'description', content: 'åœ¨çº¿æ–‡æœ¬å·®å¼‚å¯¹æ¯”å·¥å…·ï¼Œæ”¯æŒé€è¡Œå¯¹æ¯”ã€å•è¯å¯¹æ¯”å’Œå­—ç¬¦å¯¹æ¯”ï¼Œæä¾›è¯¦ç»†çš„å·®å¼‚ç»Ÿè®¡ã€‚' },
      { name: 'keywords', content: 'æ–‡æœ¬å¯¹æ¯”,å·®å¼‚å¯¹æ¯”,æ–‡æœ¬æ¯”è¾ƒ,diffå·¥å…·,åœ¨çº¿å·¥å…·' }
    ]
  },
  data() {
    return {
      originalText: '',
      modifiedText: '',
      diffMode: 'line',
      ignoreWhitespace: false,
      ignoreCase: false,
      diffResult: [],
      diffStats: {
        additions: 0,
        deletions: 0,
        modifications: 0,
        unchanged: 0
      }
    }
  },
  methods: {
    performDiff() {
      if (!this.originalText && !this.modifiedText) {
        this.diffResult = []
        this.resetStats()
        return
      }

      let original = this.originalText
      let modified = this.modifiedText

      // é¢„å¤„ç†æ–‡æœ¬
      if (this.ignoreCase) {
        original = original.toLowerCase()
        modified = modified.toLowerCase()
      }

      if (this.ignoreWhitespace) {
        original = original.replace(/\s+/g, ' ').trim()
        modified = modified.replace(/\s+/g, ' ').trim()
      }

      switch (this.diffMode) {
        case 'line':
          this.performLineDiff(original, modified)
          break
        case 'word':
          this.performWordDiff(original, modified)
          break
        case 'char':
          this.performCharDiff(original, modified)
          break
      }
    },

    performLineDiff(original, modified) {
      const originalLines = original.split('\n')
      const modifiedLines = modified.split('\n')
      
      this.diffResult = []
      this.resetStats()

      const maxLines = Math.max(originalLines.length, modifiedLines.length)
      
      for (let i = 0; i < maxLines; i++) {
        const originalLine = originalLines[i] || ''
        const modifiedLine = modifiedLines[i] || ''

        if (originalLine === modifiedLine) {
          if (originalLine || modifiedLine) {
            this.diffResult.push({
              type: 'unchanged',
              content: this.escapeHtml(originalLine),
              originalLine: i + 1,
              modifiedLine: i + 1
            })
            this.diffStats.unchanged++
          }
        } else if (!originalLine) {
          this.diffResult.push({
            type: 'addition',
            content: this.escapeHtml(modifiedLine),
            modifiedLine: i + 1
          })
          this.diffStats.additions++
        } else if (!modifiedLine) {
          this.diffResult.push({
            type: 'deletion',
            content: this.escapeHtml(originalLine),
            originalLine: i + 1
          })
          this.diffStats.deletions++
        } else {
          this.diffResult.push({
            type: 'deletion',
            content: this.escapeHtml(originalLine),
            originalLine: i + 1
          })
          this.diffResult.push({
            type: 'addition',
            content: this.escapeHtml(modifiedLine),
            modifiedLine: i + 1
          })
          this.diffStats.modifications++
        }
      }
    },

    performWordDiff(original, modified) {
      const originalWords = original.split(/\s+/).filter(w => w)
      const modifiedWords = modified.split(/\s+/).filter(w => w)
      
      this.diffResult = []
      this.resetStats()

      const lcs = this.longestCommonSubsequence(originalWords, modifiedWords)
      let i = 0, j = 0, k = 0

      while (i < originalWords.length || j < modifiedWords.length) {
        if (k < lcs.length && i < originalWords.length && originalWords[i] === lcs[k]) {
          this.diffResult.push({
            type: 'unchanged',
            content: this.escapeHtml(originalWords[i])
          })
          this.diffStats.unchanged++
          i++
          j++
          k++
        } else if (i < originalWords.length && (k >= lcs.length || originalWords[i] !== lcs[k])) {
          this.diffResult.push({
            type: 'deletion',
            content: this.escapeHtml(originalWords[i])
          })
          this.diffStats.deletions++
          i++
        } else if (j < modifiedWords.length) {
          this.diffResult.push({
            type: 'addition',
            content: this.escapeHtml(modifiedWords[j])
          })
          this.diffStats.additions++
          j++
        }
      }
    },

    performCharDiff(original, modified) {
      this.diffResult = []
      this.resetStats()

      const lcs = this.longestCommonSubsequence(original.split(''), modified.split(''))
      let i = 0, j = 0, k = 0

      while (i < original.length || j < modified.length) {
        if (k < lcs.length && i < original.length && original[i] === lcs[k]) {
          this.diffResult.push({
            type: 'unchanged',
            content: this.escapeHtml(original[i])
          })
          this.diffStats.unchanged++
          i++
          j++
          k++
        } else if (i < original.length && (k >= lcs.length || original[i] !== lcs[k])) {
          this.diffResult.push({
            type: 'deletion',
            content: this.escapeHtml(original[i])
          })
          this.diffStats.deletions++
          i++
        } else if (j < modified.length) {
          this.diffResult.push({
            type: 'addition',
            content: this.escapeHtml(modified[j])
          })
          this.diffStats.additions++
          j++
        }
      }
    },

    longestCommonSubsequence(arr1, arr2) {
      const m = arr1.length
      const n = arr2.length
      const dp = Array(m + 1).fill().map(() => Array(n + 1).fill(0))

      for (let i = 1; i <= m; i++) {
        for (let j = 1; j <= n; j++) {
          if (arr1[i - 1] === arr2[j - 1]) {
            dp[i][j] = dp[i - 1][j - 1] + 1
          } else {
            dp[i][j] = Math.max(dp[i - 1][j], dp[i][j - 1])
          }
        }
      }

      // å›æº¯æ„å»ºLCS
      const lcs = []
      let i = m, j = n
      while (i > 0 && j > 0) {
        if (arr1[i - 1] === arr2[j - 1]) {
          lcs.unshift(arr1[i - 1])
          i--
          j--
        } else if (dp[i - 1][j] > dp[i][j - 1]) {
          i--
        } else {
          j--
        }
      }

      return lcs
    },

    escapeHtml(text) {
      return text
        .replace(/&/g, '&amp;')
        .replace(/</g, '&lt;')
        .replace(/>/g, '&gt;')
        .replace(/"/g, '&quot;')
        .replace(/'/g, '&#39;')
        .replace(/\n/g, '<br>')
        .replace(/ /g, '&nbsp;')
    },

    getDiffMarker(type) {
      const markers = {
        addition: '+',
        deletion: '-',
        modification: '~',
        unchanged: ' '
      }
      return markers[type] || ' '
    },

    getLineCount(text) {
      return text ? text.split('\n').length : 0
    },

    getWordCount(text) {
      return text ? text.split(/\s+/).filter(w => w).length : 0
    },

    resetStats() {
      this.diffStats = {
        additions: 0,
        deletions: 0,
        modifications: 0,
        unchanged: 0
      }
    },

    swapTexts() {
      const temp = this.originalText
      this.originalText = this.modifiedText
      this.modifiedText = temp
      this.performDiff()
    },

    clearAll() {
      this.originalText = ''
      this.modifiedText = ''
      this.diffResult = []
      this.resetStats()
    },

    loadSampleText(type) {
      const samples = {
        original: `Hello World!
This is a sample text.
It has multiple lines.
Some lines will be changed.
Others will remain the same.`,
        modified: `Hello Universe!
This is a sample text.
It has multiple lines.
Some lines have been modified.
Others will remain the same.
This is a new line.`
      }

      if (type === 'original') {
        this.originalText = samples.original
      } else {
        this.modifiedText = samples.modified
      }
      
      this.performDiff()
    }
  }
}
</script>

